/* TYPOGRAPHY ======================*/


.App {
  text-align: center;
  
}
.controls li{
  text-align: center;
}


/* STYLES====================== */

ul.controls{
  display: flex;

}
.controls li{
  width: 30%;
  background-color: gray;
  border: 1px black solid;
}
li{
  list-style: none;
}

li:nth-child(even){
  border: gray 3px solid;
}
li:nth-child(odd){
  border: black 3px solid;
}